.App {
  text-align: center;
  width: 100vw;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.p-10 {
  padding: 10px;
}

.p-20 {
  padding: 20px;
}

.b-border {
  border: 1px solid black;
}

#portfolio-section {
  display: flex;
  height: 60vh;
  justify-content: center;
}

#new-portfolio-form {
  margin: 30px 150px;
  border-radius: 20px;
}

#edit-portfolio-section {
  display: flex;
  justify-content: center;
  align-items: center;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
